.widget-card {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.widget-toolbar {
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
  color: white;
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 16px;
  font-size: 16px;
  font-weight: 500;

  mat-icon {
    font-size: 20px;
    width: 20px;
    height: 20px;
  }
}

.widget-content {
  flex: 1;
  padding: 16px;
  display: flex;
  flex-direction: column;
  gap: 12px;
  overflow: hidden;
}

.chart-container {
  flex: 1;
  width: 100%;
  min-height: 250px;
  position: relative;

  :deep(svg) {
    width: 100%;
    height: 100%;
  }

  :deep(.x-axis),
  :deep(.y-axis) {
    path,
    line {
      stroke: var(--text-secondary);
      opacity: 0.3;
    }

    text {
      fill: var(--text-secondary);
    }
  }

  :deep(.line) {
    filter: drop-shadow(0 2px 4px rgba(79, 172, 254, 0.3));
  }

  :deep(.point) {
    filter: drop-shadow(0 2px 4px rgba(79, 172, 254, 0.4));
  }
}

.chart-info {
  text-align: center;
  padding: 8px 0;
  border-top: 1px solid rgba(0, 0, 0, 0.08);

  .info-text {
    font-size: 12px;
    color: var(--text-secondary);
    font-weight: 500;
  }
}

:host-context(.dark-theme) {
  .chart-info {
    border-top-color: rgba(255, 255, 255, 0.1);
  }
}
